import {
	MYINFO,
	JSAPI,
	GETNUMINFO
} from "../../utils/api.js"
var jweixin = require('jweixin-module')
export default {
	data() {
		return {
			infoData: "",
			imageshow: false,
			screenHight: '',
			phoneNum: ''
		}
	},
	computed: {
		getTime: function() {
			var time = this.infoData.vipEndTime
			var result = String(time).split(" ")[0]
			return result
		},
		changeTime: function() {
			var time = this.infoData.totalCallTime
			var result = 0
			if (time >= 3600 * 24) {
				result = time / 3600 / 24 + ',天'
			} else if (time >= 3600 && time <= 3600 * 24) {
				result = time / 3600 + ',小时'
			} else if (time >= 60 && time < 3600) {
				result = time / 60 + ',分钟'
			} else {
				result = time + ',秒'
			}
			return result
		},
		weekTime: function() {
			var time = this.infoData.weekCallTime
			var result = 0
			if (time >= 3600 * 24) {
				result = time / 3600 / 24 + ',天'
			} else if (time >= 3600 && time <= 3600 * 24) {
				result = time / 3600 + ',小时'
			} else if (time >= 60 && time < 3600) {
				result = time / 60 + ',分钟'
			} else {
				result = time + ',秒'
			}
			return result
		}
	},
	onLoad() {
		// 初始化数据
		this.initData()
		// 获得JSSDK
		this.getJSAPI()
		// 获取呼叫转接号码
		this.getPhoneNum()
		uni.showToast({
			title: '正在加载中..',
			icon: 'none',
			mask: false
		})
	},
	onPullDownRefresh() {
		this.initData()
	},
	methods: {
		// 初始化数据
		initData() {
			const params = {}
			this.$request.url_request(MYINFO, params, "GET", res => {
				this.infoData = JSON.parse(res.data).data
				uni.hideToast()
				uni.stopPullDownRefresh()
			}, err => {})
		},
		// 获取呼叫转接号码
		getPhoneNum(){
			const params = {}
			this.$request.url_request(GETNUMINFO,params,'GET',res=>{
				this.phoneNum = JSON.parse(res.data).data.line
			},err =>{})
		},
		// 我的钱包
		mineWalletClick() {
			uni.navigateTo({
				url: 'MineWallet/MineWallet'
			})
		},
		// 获得JSAPI
		getJSAPI() {
			var that = this
			const params = {}
			this.$request.url_request(JSAPI, params, "GET", res => {
				this.jsData = JSON.parse(res.data).data
				console.log("得到的签名", this.jsData.signature)
				jweixin.config({
						appId: this.jsData.appId,
						timestamp: this.jsData.timestamp,
						nonceStr: this.jsData.nonceStr,
						signature: this.jsData.signature,
						jsApiList: ['updateAppMessageShareData', 'onMenuShareAppMessage', 'onMenuShareTimeline']
					}),
					jweixin.ready(function() {
						console.log("接口处理成功")
					}),
					jweixin.error(function() {
						console.log("接口处理失败")
					})
			}, err => {})
		},
		// weekShare
		weekShare() {
			console.log(11)
			this.imageshow = true
			var that = this
			// 分享到朋友
			jweixin.onMenuShareAppMessage({
				title: '【魔小秘】您的专属智能来电助理', // 分享标题
				desc: '不想接，就挂断，来电助理帮你接听', // 分享描述
				link: 'https://m.checkshirt-ai.com/h5poster/index.html#/', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
				imgUrl: 'https://ai-assist.oss-cn-beijing.aliyuncs.com/aac/mxmlogo.png', // 分享图标
				type: 'link', // 分享类型,music、video或link，不填默认为link
				dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
				success: function() {
					// 用户点击了分享后执行的回调函数
					that.imageshow = false
				}
			})
			//分享到朋友圈
			jweixin.onMenuShareTimeline({
				title: '【魔小秘】您的专属智能来电助理', // 分享标题
				link: 'https://m.checkshirt-ai.com/h5poster/index.html#/',
				imgUrl: 'https://ai-assist.oss-cn-beijing.aliyuncs.com/aac/mxmlogo.png', // 分享图标
				success: function() {
					// 用户点击了分享后执行的回调函数
					that.imageshow = false
				}
			})



		},
		// 我的会员
		mineVipClick() {
			uni.navigateTo({
				url: "MineVipPage/MineVipPage"
			})
		},
		// 专属号码
		exclusiveNum(){
			uni.navigateTo({
				url:'exclusiveNum/exclusiveNum'
			})
		},
		// focus
		focus() {
			uni.navigateTo({
				url: '../IncomeHome/focus/focus'
			})
		},
		close() {
			this.imageshow = false
		},
		// 为好友省钱
		saveMoney() {
			uni.navigateTo({
				url: "../Login/Invite/Invite?invite=" + this.infoData.inviteUrl
			})
		},
		// 帮助中心
		helpCenter() {
			uni.navigateTo({
				url: '../helpCenter/helpCenter'
			})
		},
		// 微信客服
		WxService(){
			uni.navigateTo({
				url:'WxService/WxService'
			})
		},
		aaa(){
			uni.showModal({
				content: '请到公众号菜单关于我们->联系客服',
				showCancel: false
			});
		},
		haveAWord() {
			uni.navigateTo({
				url: 'haveASaying/haveASaying'
			})
		},
		// 绿色通道
		greenChannel(){
			uni.report('mineGreen', '个人中心绿色通道')
			uni.navigateTo({
				url:'../greenChannel/greenChannel'
			})
		}
	}
}
